.projectCard {
    height: 40px;
    fill: white;
    transition: all 0.1s ease-in-out;
    cursor: pointer;
}

.projectCard:hover {
    height: 45px;
    fill: tomato;
}

.footer {
    height: 30px;
    fill: white;
    transition: all 0.18s ease-in-out;
    cursor: pointer;
    padding: 0px 10px 0px 10px;
}

.footer:hover {
    height: 35px;
}

.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
}